import { NoticeBar, Swiper, WhiteSpace } from "@taroify/core"
import { ArrowRight, Cross, InfoOutlined, VolumeOutlined } from "@taroify/icons"
import * as React from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicNoticeBar() {
  return (
    <NoticeBar scrollable>
      <NoticeBar.Icon>
        <VolumeOutlined />
      </NoticeBar.Icon>
      
    </NoticeBar>
  )
}

function ScrollableNoticeBar() {
  return (
    <>
      <NoticeBar scrollable></NoticeBar>
      <WhiteSpace />
      <NoticeBar scrollable={false}>
        
      </NoticeBar>
    </>
  )
}

function WordwrapNoticeBar() {
  return (
    <NoticeBar wordwrap scrollable={false}>
      
    </NoticeBar>
  )
}

function ActionNoticeBar() {
  return (
    <>
      <NoticeBar scrollable={false}>
        
        <NoticeBar.Action>
          <Cross />
        </NoticeBar.Action>
      </NoticeBar>
      <WhiteSpace />
      <NoticeBar scrollable={false}>
        
        <NoticeBar.Action>
          <ArrowRight />
        </NoticeBar.Action>
      </NoticeBar>
    </>
  )
}

function NoticeBarWithCustomStyle() {
  return (
    <NoticeBar style={{ color: "#1989fa", background: "#ecf9ff" }}>
      <NoticeBar.Icon>
        <InfoOutlined />
      </NoticeBar.Icon>
      
    </NoticeBar>
  )
}

function NoticeBarWithVerticalScrolling() {
  return (
    <NoticeBar>
      <NoticeBar.Icon>
        <VolumeOutlined />
      </NoticeBar.Icon>
      <Swiper className="notice-swiper" direction="vertical" autoplay={3000}>
        <Swiper.Item> 1</Swiper.Item>
        <Swiper.Item> 2</Swiper.Item>
        <Swiper.Item> 3</Swiper.Item>
      </Swiper>
    </NoticeBar>
  )
}

export default function NoticeBarDemo() {
  return (
    <Page title="NoticeBar " className="notice-bar-demo">
      <Block title="">
        <BasicNoticeBar />
      </Block>
      <Block title="">
        <ScrollableNoticeBar />
      </Block>
      <Block title="">
        <WordwrapNoticeBar />
      </Block>
      <Block title="">
        <ActionNoticeBar />
      </Block>
      <Block title="">
        <NoticeBarWithCustomStyle />
      </Block>
      <Block title="">
        <NoticeBarWithVerticalScrolling />
      </Block>
    </Page>
  )
}
